// src/App.js
import React, { useEffect, useState } from "react";
import "./App.css";
import { computeWeightedStats, recommendHand } from "./algorithms";
import { loadRecords, saveRecord, deleteRecord, migrateLocalToSupabase } from "./db";
import Stats from "./components/Stats";

const HANDS = [
  { id: "rock", emoji: "", label: "" },
  { id: "scissors", emoji: "", label: "" },
  { id: "paper", emoji: "", label: "" },
];

function App() {
  const [tab, setTab] = useState("input");
  const [records, setRecords] = useState([]);
  const [date, setDate] = useState(new Date().toISOString().slice(0,10));
  const [selectedHand, setSelectedHand] = useState("rock");
  const [result, setResult] = useState("draw");
  const [recencySensitivity, setRecencySensitivity] = useState(0.4);
  const [expectedVsWinRate, setExpectedVsWinRate] = useState(0.5);
  const [supabaseAvailable, setSupabaseAvailable] = useState(false);

  // load records async
  useEffect(() => {
    (async () => {
      try {
        const rs = await loadRecords();
        setRecords(rs || []);
        // if supabase configured, mark available
        setSupabaseAvailable(!!process.env.REACT_APP_SUPABASE_URL && !!process.env.REACT_APP_SUPABASE_ANON_KEY);
      } catch (e) {
        console.error("loadRecords error", e);
        setRecords([]);
      }
    })();
  }, []);

  // average score safe calc
  const validScores = (records || []).map(r => Number(r.score)).filter(v => Number.isFinite(v));
  const averageScore = validScores.length ? (validScores.reduce((a,b)=>a+b,0)/validScores.length).toFixed(2) : "0.00";

  // stats & recommendation
  const stats = computeWeightedStats(records, new Date(date), recencySensitivity);
  const recommendedHand = recommendHand(stats, expectedVsWinRate);
  const finalScore = Number.isFinite(stats[recommendedHand]?.avgScore) ? Math.round(stats[recommendedHand].avgScore) : 0;

  const refreshRecords = async () => {
    const rs = await loadRecords();
    setRecords(rs || []);
  };

  const onSave = async () => {
    // fixed scoring: win=30, draw=20, loss=10
    const score = result === "win" ? 30 : result === "draw" ? 20 : 10;
    const rec = {
      id: Date.now().toString(),
      date,
      hand: selectedHand,
      result,
      score
    };
    try {
      await saveRecord(rec);
      await refreshRecords();
    } catch (e) {
      console.error("saveRecord failed", e);
      alert("");
    }
  };

  const onDelete = async (id) => {
    try {
      await deleteRecord(id);
      await refreshRecords();
    } catch (e) {
      console.error("deleteRecord failed", e);
      alert("");
    }
  };

  const onMigrateLocal = async () => {
    try {
      const res = await migrateLocalToSupabase();
      alert(`migrated ${res.migrated} records to Supabase`);
      await refreshRecords();
    } catch (e) {
      console.error(e);
      alert("Migration failed: " + (e.message || e));
    }
  };

  return (
    <div className="app">
      <header className="header">
        <h1></h1>
        <div className="tabs">
          <button className={tab==="input"?"active":""} onClick={()=>setTab("input")}></button>
          <button className={tab==="stats"?"active":""} onClick={()=>setTab("stats")}></button>
        </div>
      </header>

      <main className="container">
        <div className="top-row">
          <div className="avg-block">
            <div className="label"></div>
            <div className="avg-value">{averageScore}</div>
          </div>
          <div style={{marginLeft:20}}>
            <div style={{fontSize:12,color:"#777"}}>Cloud sync: {supabaseAvailable ? "ready" : "disabled"}</div>
            {supabaseAvailable && (
              <button onClick={onMigrateLocal} style={{marginTop:8}}> Supabase </button>
            )}
          </div>
        </div>

        <div className="slider-row">
          <label>Recency sensitivity</label>
          <input type="range" min="0" max="1" step="0.01" value={recencySensitivity} onChange={e=>setRecencySensitivity(Number(e.target.value))}/>
        </div>

        <div className="slider-row">
          <label>Expected vs WinRate</label>
          <input type="range" min="0" max="1" step="0.01" value={expectedVsWinRate} onChange={e=>setExpectedVsWinRate(Number(e.target.value))}/>
        </div>

        {tab==="input" && (
          <>
            <div className="date-row">
              <label></label>
              <input type="date" value={date} onChange={e=>setDate(e.target.value)} />
            </div>

            <div className="recommendation">
              <div className="final">Recommendation <strong>FinalScore: {finalScore}</strong></div>
              <div className="cards">
                {HANDS.map(h=>{
                  const s = stats[h.id];
                  return (
                    <div key={h.id} className={`card ${recommendedHand===h.id?'highlight':''}`}>
                      <div className="emoji">{h.emoji}</div>
                      <div className="label">{h.label}</div>
                      <div className="plays">{s && s.plays ? Math.round(s.plays) : 0} plays</div>
                      <div className="avg">{Number.isFinite(s?.avgScore) ? s.avgScore.toFixed(2) : "0.00"}</div>
                    </div>
                  );
                })}
              </div>
            </div>

            <div className="result-buttons">
              <div className={`pill ${result==="win"?"selected":""}`} onClick={()=>setResult("win")}></div>
              <div className={`pill ${result==="draw"?"selected":""}`} onClick={()=>setResult("draw")}></div>
              <div className={`pill ${result==="loss"?"selected":""}`} onClick={()=>setResult("loss")}></div>
            </div>

            <div className="hand-select">
              {HANDS.map(h=>(
                <button key={h.id} className={`hand ${selectedHand===h.id?"active":""}`} onClick={()=>setSelectedHand(h.id)}>
                  {h.emoji} {h.label}
                </button>
              ))}
            </div>

            <div className="actions">
              <button className="primary" onClick={onSave}></button>
            </div>

            <div className="records-table">
              <table>
                <thead><tr><th></th><th></th><th></th><th></th><th></th></tr></thead>
                <tbody>
                  {[...records].reverse().map(r=>(
                    <tr key={r.id}>
                      <td>{r.date}</td>
                      <td>{HANDS.find(h=>h.id===r.hand)?.emoji}</td>
                      <td>{r.result==="win"?"":r.result==="draw"?"":""}</td>
                      <td>{r.score}</td>
                      <td><button className="del" onClick={()=>onDelete(r.id)}></button></td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          </>
        )}

        {tab==="stats" && <Stats records={records} />}

      </main>
    </div>
  );
}

export default App;
